import React, { useState } from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import { restoreState } from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function filterNumber(num: number) {
    return num < 10 ? '0' + num : num;
}

function Clock() {
    const [timerId, setTimerId] = useState<ReturnType<typeof setInterval> | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)

    const start = () => {
        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)
        setTimerId(setInterval(() => {
            console.log('1');
            
            setDate(new Date(restoreState('hw9-date', Date.now())))
        }, 1000))
    }

    const stop = () => {
        //   //    ,    (timerId <- undefined)
        clearInterval(timerId)
        setTimerId(undefined)
    }

    const onMouseEnter = () => {
        setShow(true)
    }
    const onMouseLeave = () => { //   //      
        setShow(false)
    }

    let stringTime = `${filterNumber(date.getHours())}:${filterNumber(date.getMinutes())}:${filterNumber(date.getSeconds())}` || <br /> // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
    const stringDate = `${filterNumber(date.getDate())}.${filterNumber(date.getMonth() + 1)}.${date.getFullYear()}` || <br /> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    let dayToString = new Intl.DateTimeFormat("en", { weekday: "long" });
    let monthToString = new Intl.DateTimeFormat("en", { month: 'long' });
    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = dayToString.format(date) || <br /> //  
    const stringMonth = monthToString.format(date) || <br /> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br />
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={!!timerId} //   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={!timerId} //   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
